@import url('/fss/commons/css/common.css');
@import '/fss/fss/css/default.css';
@import 'breakpoint/breakpoint';
@import 'base/variables';
@import 'base/color';
@import 'mixins/mixins'; //mixins
@import 'mixins/layerfixed'; //mixins
@import 'components/sprite';
@import "components/slidectrl";
@import 'components/typography';

img{max-width:100%;}
button{border:none;}
@mixin imgsize(){
	width:325px;
	height:313px;
}
.globalset{
	height:50px;
	background-color:#008bce;
	display:none;
	@include breakpoint($lg){
		display:block;
	}
	.set{
		max-width:1276px;
		margin: 0 auto;
		position:relative;
		.l{
			float:left;
			li{float:left;
				padding-right:10px;
				&:last-child{display:none;}
				@include breakpoint($xl){padding-right:20px;
					&:last-child{display:block;}
				}
			}
		}
		.r{
			position:absolute;
			right:0;
			top:0;

		}
	}
}
@mixin maw() {
	max-width:1276px;
	margin: 0 auto;
};
#header{
	max-width:1276px;
	margin: 0 auto;
	position:relative;
	.t{
		height:100px;
	}
	.logo{
		position:absolute;
		left:10px;
		top:19px;
		@include breakpoint($xl){left:0;}
	}
	.sh{
		text-align:center;
		padding-top:24px;
		display:none;
		@include breakpoint($lg){
			display:block;
		}
		.a{
			margin-right:20px;
			display:inline-block;
		}
		.b{display:none;
			@include breakpoint($xl){display:inline-block;}
		}
	}
	.wader{
		position:absolute;
		right:10px;
		top:22px;
		@include breakpoint($xl){right:0;}
		@include breakpoint($sm){img{width:120px;padding-top:8px;}}
	}
}
#gnb{
	padding-bottom:10px;
	margin:0 10px;
	@include breakpoint($xl){}
	@include breakpoint($lg){height:49px;padding-bottom:0;margin:0 32px;}
	position:relative;
	ul{
		li{
			float:left;
			// 0.12
			margin-right:10%;
			display:none;
			@include breakpoint($lg){display:block;}
			@include breakpoint($xl){
				margin-right:12%;
			}
			a{}
			&:last-child{
				@include breakpoint(max-width 991px){
					position:absolute;
					right:0;
					top:12px;
				}
				display:block;
				margin-right:0;
				float:right;
				// padding-right:32px;
			}
		}
	}
	.sh2{
		display:block;
		position:relative;
		padding-right:50px;
		input{
			height:50px;
			border:1px solid #008bce;
			width:100%;
		}
		button{
			position:absolute;
			right:50px;
			top:0;
			width:50px;
			height:50px;
			background: url(../images/btnsh.png) 50% 50%  no-repeat;
		}
		@include breakpoint($lg){display:none;}
	}
}
#c1{
	@include maw;
	.top{
		.l{
			text-align:center;
			background-color:#f8f7f5;
		}
		.r{
			padding:20px;
			background: url(../images/visualbg.png) 0 0 no-repeat;
			background-size:cover;
			position:relative;
			padding-bottom:50px;
			@include breakpoint($md){
				padding:20px;
			}
			@include breakpoint($lg){
				padding:39px 0 0 59px;
			}
			img{
				width:70%;
				@include breakpoint($xl){width:auto;}
			}
			.c1ctrl{
				position:absolute;
				left:20px;
				bottom:10px;
				@include breakpoint($md){
					// left:60px;
				}
				@include breakpoint($lg){
					left:60px;
					// bottom:10px;
				}
				@include breakpoint($xl){
					left:60px;
					bottom:30px;
				}
			}
		}
		@include breakpoint($md){
			display:table;
			width:100%;
			>*{display:table-cell;vertical-align:top;}
			.l{
				width:32.2884012539185%;
			}
			.r{

				background: url(../images/visualbg.png) 0 0 no-repeat;
				background-size:cover;
				img{
					width:70%;
					@include breakpoint($xl){width:auto;}
				}
			}
		}
	}
	.noticeset{
		background-color:#008bce;
		color:#fff;
		padding:10px;
		@include breakpoint($md){padding:15px 40px;}
	}
	.row{
		.s{
			position:relative;
			padding-left:87px;
		}
		h1{
			font-size:15px;
			font-weight:bold;
			// 87+20
			width:87px;
			position:absolute;
			left:0;
			top:3px;
			text-align:center;
		}
		a{
			display:block;
			color:#fff;
			text-overflow:ellipsis;overflow:hidden;white-space:nowrap;
			position:relative;
			padding-right:20px;
			background:url(../images/noticemore.png) right center no-repeat;
		}
		@include breakpoint($xl){
			.s{
				padding-left:87px+20;
			}
			h1{
				width:87px+20;
			}
			a{}
		}
	}
}

.nav{
	overflow:hidden;
	padding-top:30px-10;
	ul{
		@include breakpoint($md){float:right;}
		li{
			float:left;
			width:25%;
			@include breakpoint($md){width:auto;}
			text-align:center;
			a{
				display:block;
				@include breakpoint($md){
					width:110px;
				}
				@include breakpoint($lg){
					width:150px;
				}
				border-bottom:2px solid #fff;
				text-align:center;
				font-family:'Noto-Regular';
				font-size:16px;
				padding:10px 0;
				color:#757575;
			}
			&:last-child{}
		}
		.on{
			a{
				border-bottom-color:#e8303a;
				color:#141414;
			}
		}
	}
}

#fav{
	.s{
		@include maw;
		min-height:197px;
		position:relative;
	}
	h1{
		font-family:'Noto-Medium';
		font-size:20px;
		padding:20px 20px 10px;
		@include breakpoint($md){
			padding:0;
			position:absolute;
			left:20px;
			top:20px;
			top:30px;
		}
		@include breakpoint($xl){left:0;}
	}
	.nav{
		@include breakpoint($sm){
			padding-top:0;padding-left:10px;
		}
	}
	.ctrl{
		button{
			display:none;
			@include breakpoint($md){display:block;}
			position:absolute;
			top:102px;
			background: url(../images/favctrl.png) 0 0 no-repeat;
			width:22px;
			height:22px;
		}
		.left{
			left:22px;
		}
		.right{
			right:22px;
			background-position:right 0;
		}
	}
	.listset{
		overflow-x:hidden;
		padding:15px 50px;
		@include breakpoint($md){
			padding:20px 60px;
			padding-bottom:0;
		}
		@include breakpoint($lg){
			padding:20px 123px;
			padding-bottom:0;
		}
		ul{
			width:2000px;
		}
		li{
			float:left;
			margin-right:2.2%;
			@include breakpoint($xl){margin-right:80px;}
			a{
				text-align:center;
				display:block;
				span{display:block;}
				.i{
					padding-bottom:10px;
					height:50px;
					display: flex;
					flex-direction: row;
					justify-content: center;
					align-items: center;
				}
			}
		}
	}
}
#popupzone{
	padding:40px 0;
	background-color:#f8f7f5;
	.s{
		@include maw;
		position:relative;
		h1{
			font-size:20px;
			font-family:'Noto-Medium';
			margin-bottom:23px;
			margin-left:20px;
			@include breakpoint($xl){margin-left:0;}
		}
		.ctrl{
			position:absolute;
			top:0;
			right:22px;
		}
		.listset{
			// padding:0 20px;
			// padding:0 15px;
			// overflow:hidden;
			padding:0 10px;
			text-align:center;
			@include breakpoint($xl){padding:0;}
			.row>div{margin-bottom:10px;}
		}
	}
	.s2{
		@extend .s;
		h1{
			margin-top:49px;
		}
		.listset{
			text-align:left;
			a{
				display:block;
				background-color:#fff;
				.t{
					display:block;
					background: url(../images/icomediamore.png) right 27px no-repeat;
					padding:27px 0 17px 20px;
					@include breakpoint($xl){padding-left:32px;}
					font-size:18px;
					font-family:'Noto-Medium';
				}
				.img{
					img{width:100%;}
				}
				.txt{
					padding:16px 17px 25px 20px;
					@include breakpoint($xl){
						padding:16px 17px 25px 32px;
					}
					font-weight:bold;
					span.t2{
						font-weight:normal;
						text-overflow:ellipsis;overflow:hidden;white-space:nowrap;
					}
					span{
						// padding-top:7px;
						font-weight:normal;
						display:block;
					}
				}
			}
		}
	}
}

#info{
	@include maw;
	position:relative;
	h1{
		font-size:20px;
		font-family:'Noto-Medium';
		padding:20px;
		margin-bottom:0;
		@include breakpoint($lg){
			padding:0;
			margin-bottom:23px;
			position:absolute;
			left:20px;
			top:52px;
		}
		@include breakpoint($xl){left:0;}
	}
	.nav{
		padding-top:0;
		@include breakpoint($lg){padding-top:42px;}
		ul{
			padding:0 10px;
			@include breakpoint($lg){padding:0;}
			li{
				a{
					width:90px;
					@include breakpoint($xl){
						width:105px;
					}
				}
			}
		}
	}
	.listset{
		margin-top:30px;
		overflow:hidden;
		padding:0 10px;
		@include breakpoint($xl){padding:0;}
		.row>div{
			margin-bottom:10px;
		}
		.news{
			border:1px solid #cecece;
			background: url(../images/pt.png) repeat;
			padding:33px 29px 40px;

			a.t{
				display:block;
				background: url(../images/dotline.png) 0 bottom repeat-x;
				padding-bottom:10px;
				margin-bottom:10px;
				.cate{
					font-family:'Noto-Medium';
					font-size:16px;
					color:#008bce;
					line-height:1;
					margin-bottom:16px;
				}
				.tit{
					font-size:18px;
					font-weight:bold;
					text-overflow:ellipsis;overflow:hidden;white-space:nowrap;
				}
				.date{
					font-size:12px;
				}
				.txt{
					font-size:15px;
					color:#757575;
					padding-top:10px;
					line-height:18px;
					height:36px+10;
					overflow:hidden;
				}
			}
			ul{
				margin-bottom:15px;
				li{
					padding-left:11px;
					position:relative;
					&:before {
						content: '';
						position:absolute;
						width:2px;
						height:2px;
						background-color:#666;
						left:1px;
						top:9px;
					}
				}
				a{
					color:#757575;
					display:block;
					text-overflow:ellipsis;overflow:hidden;white-space:nowrap;
					.txt{}
					.date{padding-left:20px;}
				}
			}
			a.more{
				display:inline-block;
				border-radius:50px;
				padding:6px 21px;
				line-height:1;
				border:1px solid #cecece;
				background-color:#f8f7f5;
			}
		}
	}
	.i1{
		position:relative;
		display:block;
		padding-bottom:20px;
		.s{
			// height:150px;
			@include breakpoint($md){
				// height:289px;
			}
			display:block;
			background-position: center center;
			background-repeat:no-repeat;
			background-size:cover;

		}
		img{transition:all 0.3s ease-in-out;}
		.bg{
			display:block;
			overflow:hidden;
			height:150px;
			@include breakpoint($md){
				height:281px;
			}
			transition:all 0.3s ease-in-out;
			display: flex;
			flex-direction: row;
			justify-content: center;
			align-items: center;
			img{
				vertical-align:top;
				width:100%;
				@include breakpoint($md){
					height:100%;
					width:auto;
					max-width:inherit;
				}
				@include breakpoint($xl){
					width:100%;
				}
			}
		}
		&:hover,
		&:focus {
			.bg{
				img{
					transform:scale(1.1,1.1) rotate(2deg);
				}
				// background-size:110%;
			}
		}
		.cate2{
			position:absolute;
			left:0;top:0;
			padding:16px 18px;
			background-color:rgba(0,139,206, 0.75);
			color:#fff;
		}
		>p{
			text-overflow:ellipsis;overflow:hidden;white-space:nowrap;
			padding:20px 0 0 10px;
			strong{padding-right:20px;}
		}
	}
	.i2{
		padding:20px;
		background: url(../images/c0.jpg) right bottom no-repeat;
		background-size:cover;
		@include breakpoint($md){
			padding:33px 40px;
			height:289px;
		}

		h2{
			font-size:16px;
			font-family:'Noto-Medium';
			color:#008bce;
			margin-bottom:20px;
			line-height:1;
		}
		ul{
			li{
				@include breakpoint($md){
					width:50%;
					float:left;
				}
				a{
					padding:4px 0;
					padding-left:16px;
					background: url(../images/s2bu.png) 0 center no-repeat;
					display:block;
					font-size:16px;
					@include breakpoint($lg){font-size:18px;}
					// font-weight:bold;
				}
			}
		}
	}
}
#footer{
	.ad{
		background-color:#f8f7f5;
		.set{
			@include maw;
			overflow:hidden;
			padding:0 10px;
			@include breakpoint($xl){padding:0;}
		}
		text-align:center;
		padding-bottom:10px;
		@include breakpoint($lg){
			text-align:left;
			padding-bottom:0;
			.l{float:left;}
			.r{float:right;padding-top:20px;}
		}
	}
	.bottom{
		@include maw;
		font-size:15px;
		padding:22px 10px 0;
		text-align:center;
		@include breakpoint($xl){padding:22px 0 0;}
		.l{
			padding-bottom:20px;
			@include breakpoint($xl){padding-bottom:50px;}
			.fnav{
				padding-top:8px;
				ul{
					overflow:hidden;
					margin-bottom:28px-6;
				}
				li{
					display:inline-block;
					a{font-size:15px;}
					&:first-child a{color:#e8303a;}
					&+li{margin-left:10px;padding:8px 0 0;}

					@include breakpoint($md){
						&+li{margin-left:43px;}
					}
				}
			}
			span{color:#e8303a;}
			p+p{
				margin-top:20px-6;
			}
		}
		.r{padding-bottom:50px;
			@include breakpoint($xl){}
			span+span{padding-left:30px;}
			img{vertical-align:top;}
		}
		@include breakpoint($xl){
			.l{
				float:left;
			}
			.r{
				float:right;
			}
		}
	}
}
.animated { visibility: hidden; }
.animatedKey { visibility: visible !important; }
.visible {visibility: visible; }

.img{
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	overflow:hidden;
	img{
		width:100%;
		transition:all 0.3s ease-in-out;
		vertical-align:top;
	}
	&:hover,
	&:focus{
		img{
			transform:scale(1.1,1.1) rotate(2deg);
		}
	}
}
.iv{
	&:hover,
	&:focus{
		img{
			transform:scale(1.1,1.1) rotate(2deg);
		}
	}
}
.iv2{
	img{
		transition:all 0.3s ease-in-out;
	}
	&:hover,
	&:focus{
		img{
			transform:scale(1.1,1.1);
			box-shadow: 0 0 3px 3px hsla(0,0%,0%,.25);
		}
	}
}
.iv3{
	img{
		transition:all 0.3s ease-in-out;
	}
	&:hover,
	&:focus{
		img{
			transform:scale(1.1,1.1);
			// box-shadow: 0 0 3px 3px hsla(0,0%,0%,.25);
		}
	}
}